.closeVer1 {
  background: radial-gradient(circle at top left, white 5%, red 95%);
  border-radius: 50%;
  width: 25px;
  height: 25px;
  color: white;
  font-weight: bold;
  border: 2px solid black;
  display: flex;
  align-items: center;
  justify-content: center; }
  .closeVer1 div:first-child {
    width: 5px;
    height: 17px;
    background-color: white;
    transform: rotate(45deg);
    border-radius: 10px; }
  .closeVer1 div:last-child {
    width: 5px;
    height: 17px;
    margin-left: -5px;
    background-color: white;
    transform: rotate(-45deg);
    border-radius: 10px; }

.closeVer1:active div:first-child {
  animation-name: anim1a;
  animation-duration: 0.25s;
  background-color: blue;
  transform: rotate(180deg); }

.closeVer1:active div:last-child {
  animation-name: anim2a;
  animation-duration: 0.25s;
  background-color: blue;
  transform: rotate(-90deg); }

@keyframes anim1a {
  from {
    background-color: white;
    transform: rotate(45deg); }
  to {
    background-color: blue;
    transform: rotate(180deg); } }

@keyframes anim2a {
  from {
    background-color: white;
    transform: rotate(-45deg); }
  to {
    background-color: blue;
    transform: rotate(-90deg); } }

